import axios, {AxiosResponse} from 'axios'
import {FormikErrorType} from "./Login";
import {FormikRegisterErrorType} from "../registration/Registration";

const instance = axios.create({
    baseURL: "https://neko-back.herokuapp.com/2.0/",
    withCredentials: true,
})

export const authAPI = {
    login(data: FormikErrorType) {
        return instance.post<FormikErrorType, AxiosResponse<ResponseDataType>>('/auth/login', data)
    },
    register(data: FormikRegisterErrorType) {
        return instance.post<FormikRegisterErrorType, AxiosResponse<RegisterDataType>>('/auth/register', data)
    },
    me() {
        return instance.post< AxiosResponse<ResponseDataType>>('/auth/me')
    },
    logOut() {
        return instance.delete< AxiosResponse<DataType>>('/auth/me')
    },
    forgot(data: ForgotDataType) {
        return instance.post<ForgotDataType, AxiosResponse<DataType>>('/auth/forgot', data)
    },
    createNewPassword(data: NewPasswordType) {
        return instance.post<NewPasswordType, AxiosResponse<DataType>>('/auth/set-new-password', data)
    },
}
export type NewPasswordType = {
    password: string,
    resetPasswordToken: string
}
export type ForgotDataType = {
    email: string,
    from: string
    message: string
}

export type RegisterDataType = {
    addedUser: {},
    error?: string;
}
export type DataType = {
    info: string,
    error?: string;
}
export type ResponseDataType = {

    _id: string;
    email: string;
    name: string;
    avatar?: string;
    publicCardPacksCount: number; // количество колод

    created: Date;
    updated: Date;
    isAdmin: boolean;
    verified: boolean; // подтвердил ли почту
    rememberMe: boolean;

    error?: string;

}