import {authAPI, ForgotDataType, NewPasswordType, ResponseDataType} from "./loginApi";
import {FormikErrorType} from "./Login";
import {Dispatch} from "redux";

export type RequestStatusType = 'idle' | 'loading' | 'succeeded' | 'failed'
const initialState = {
    isLoggedIn: false,
    data: {} as ResponseDataType,
    error: '',
    isInitialised: false,
    status: 'idle' as RequestStatusType,
    isNewPasswordSet: 'idle' as RequestStatusType,
}

export const loginReducer = (state: InitialStateType = initialState, action: ActionType): InitialStateType => {
    switch (action.type) {

        case "Login/SET-IS-LOGGED-IN":
            return {...state, isLoggedIn: action.value};
        case "Login/SET-DATA":
            return {...state, data: action.data}
        case "Login/SET-ERROR":
            return {...state, error: action.error}
        case "Login/SET-STATUS":
            return {...state, status: action.status}
        case "Login/SET-IS-INITIALIZED":
            return {...state, isInitialised: action.value}
        case "Login/SET-NEW-PASSWORD":
            return {...state, isNewPasswordSet: action.value}
        default:
            return state;
    }
};

// actions
const setErrorAC = (error: string) =>
    ({type: "Login/SET-ERROR", error} as const)
export const setIsLoggedInAC = (value: boolean) =>
    ({type: 'Login/SET-IS-LOGGED-IN', value} as const)
export const setDataAC = (data: ResponseDataType) =>
    ({type: 'Login/SET-DATA', data} as const)
export const setIsInitializedAC = (value: boolean) =>
    ({type: 'Login/SET-IS-INITIALIZED', value} as const)
export const setStatusAC = (status: RequestStatusType) =>
    ({type: "Login/SET-STATUS", status} as const)
export const setNewPasswordAC = (value: RequestStatusType) =>
    ({type: "Login/SET-NEW-PASSWORD", value} as const)

// types
type InitialStateType = typeof initialState;
export type ActionType = ReturnType<typeof setErrorAC>
    | ReturnType<typeof setIsLoggedInAC>
    | ReturnType<typeof setDataAC>
    | ReturnType<typeof setIsInitializedAC>
    | ReturnType<typeof setStatusAC>
    | ReturnType<typeof setNewPasswordAC>


// thunks
export const loginTC = (data: FormikErrorType) => (dispatch: Dispatch<ActionType>) => {

    authAPI.login(data)
        .then(res => {
            dispatch(setIsLoggedInAC(true))
            dispatch(setDataAC(res.data))
        })
        .catch(e => {
            const error = e.response
                ? e.response.data.error
                : (e.message + ', more details in the console');
            dispatch(setErrorAC(error))
        })
}
export const isInitialisedTC = () => (dispatch: Dispatch<ActionType>) => {

    authAPI.me()
        .then(res => {
            dispatch(setIsInitializedAC(true))
        })
        .catch(e => {
            const error = e.response
                ? e.response.data.error
                : (e.message + ', more details in the console');
            dispatch(setErrorAC(error))
        })
}
export const logOutTC = () => (dispatch: Dispatch<ActionType>) => {

    authAPI.logOut()
        .then(res => {
            dispatch(setIsLoggedInAC(false))

        })
        .catch(e => {
            const error = e.response
                ? e.response.data.error
                : (e.message + ', more details in the console');
            dispatch(setErrorAC(error))
        })
}

export const forgotTC = (data: ForgotDataType) => (dispatch: Dispatch<ActionType>) => {
    dispatch(setStatusAC('loading'))
    authAPI.forgot(data)
        .then(res => {
            dispatch(setStatusAC('succeeded'))
        })
        .catch(e => {
            const error = e.response
                ? e.response.data.error
                : (e.message + ', more details in the console');
            dispatch(setErrorAC(error))
        })
}

export const createNewPasswordTC = (data: NewPasswordType) => (dispatch: Dispatch<ActionType>) => {
    dispatch(setNewPasswordAC('loading'))
    authAPI.createNewPassword(data)
        .then(res => {
            dispatch(setNewPasswordAC('succeeded'))
        })
        .catch(e => {
            const error = e.response
                ? e.response.data.error
                : (e.message + ', more details in the console');
            dispatch(setErrorAC(error))
        })
}