import React, {ChangeEvent, useState} from "react";
import {NavLink} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {forgotTC, RequestStatusType} from "../login/loginReducer";
import {AppRootStateType} from "../../app/store";

export const PasswordRecovery: React.FC = () => {
    const status = useSelector<AppRootStateType, RequestStatusType>(state => state.login.status)
    const dispatch = useDispatch()
    const [data, setData] = useState(
        {
            email: '',
            from: "test-front-admin <ai73a@yandex.by>", // можно указать разработчика фронта)
            message: `<div style="background-color: lime; padding: 15px">
            password recovery link: 
            <a href='https://beergrizl.github.io/newcards/#/setPassword/$token$'>
link</a></div>` // хтмп-письмо, вместо $token$ бэк вставит токен,
        }
    )
    const onClickHandler = () => {
        dispatch(forgotTC(data))
    }
    const onChangeHandler = (e: ChangeEvent<HTMLInputElement>) => {
        setData({...data, email: e.currentTarget.value})
    }
    if(status==='succeeded'){
        return <div>
            <h1> Check email</h1>
            <p>We send an email with instructions to {data.email}</p>
        </div>
    }

    return (
        <div>
            <h1> PasswordRecovery</h1>
            email <input type={'email'} onChange={onChangeHandler}/>
            <button onClick={onClickHandler}>Send</button>
            <p>Enter your email address and we will send you further instructions</p>
            <NavLink to={'/login'}>Login</NavLink>
        </div>
    )
}