import React, {ChangeEvent, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {Navigate, useParams} from "react-router-dom";
import {createNewPasswordTC, RequestStatusType} from "../login/loginReducer";
import {AppRootStateType} from "../../app/store";


export const SetPassword: React.FC = () => {
    const isNewPasswordSet=useSelector<AppRootStateType, RequestStatusType>(state => state.login.isNewPasswordSet)
    const dispatch = useDispatch()
    const [data, setData] = useState(
        {
            password: '',
            resetPasswordToken: ''
        }
    )
    const {token} = useParams<string>()
    const onChangeHandler = (e: ChangeEvent<HTMLInputElement>) => {
        setData({...data, password: e.currentTarget.value})
    }
    const onClickHandler = () => {
        if (token) {
            dispatch(createNewPasswordTC({...data, resetPasswordToken: token}))
        }
    }
    if (isNewPasswordSet==='succeeded'){
        return <Navigate to={'/login'}/>
    }
    return (
        <div>
            <h1> Create new password</h1>
            <input onChange={onChangeHandler} type={'password'}/>
            <button onClick={onClickHandler}>Create new password</button>
            <p>Create new password and we will send you further instructions to email</p>
        </div>
    )
}

